/*    */

.overlay {
  /*    */
  opacity: 0;
  visibility: hidden;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
  transition: 0.3s all;
 }

/*     */

.modal {
  /*    */
  opacity: 0;
  visibility: hidden;

  /*      */
  width: 100%;
  max-width: 280px;

  @include tablet {
    max-width: 568px;
  }

  //   

  /*     z-index */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 30; /*      */

  /*      */
  box-shadow: 0 3px 10px -0.5px rgba(0, 0, 0, 0.2);
  // text-align: center;
  border-radius: 10px;
  background-color: #fcfcfc;
  transition: 0.3s all;
  padding: 28px;
  border: 2px solid $link-bg-color;

  @include tablet {
    padding: 32px 60px;
  }
}

/*        */

.modal.active,
.overlay.active {
  opacity: 1;
  visibility: visible;
  overflow-y: scroll;
}

//   

/*     */

.modal__cross {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;

  @include tablet {
    width: 24px;
    height: 24px;
  }
}

